<template>
  <v-data-table
    :headers="headers"
    :items="itemsData"
    sort-by="crated_at"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar
        flat
      >

        <v-toolbar-title>List</v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog
          v-model="dialog"
          max-width="500px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              color="primary"
              dark
              class="mb-2"
              v-bind="attrs"
              v-on="on"
            >
              New Payment Method
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                    cols="12"
                  >

                    <v-autocomplete
                        v-model="editedItem.name"
                        :items="currencys"
                        label="Currency name"
                        required
                        :rules="[]"
                        item-text="entity"
                        item-value="id"
                        outlined

                    >
                        <template v-slot:selection="{ item }">
                            <v-list-item-action-text  class="text-truncate">{{item.entity}}</v-list-item-action-text>
                        </template>
                    </v-autocomplete>
                  </v-col>
                  <v-col
                    cols="12"
                  >
                    <v-autocomplete
                        v-model="editedItem.ids"
                        :items="ids"
                        label="Methods"
                        item-text="name"
                        item-value="id"
                        return-object
                        outlined
                        dense
                        multiple
                    >
                        <template v-slot:selection="{ item }">
                            <v-chip
                                class="ma-2"
                                small>
                                {{ item.name.replaceAll('_', ' ').toUpperCase() }}
                            </v-chip>

                        </template>

                    </v-autocomplete>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="close"
              >
                Cancel
              </v-btn>
              <v-btn
                color="blue darken-1"
                text
                @click="save"
              >
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="headline">Are you sure you want to delete this item?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
      <v-icon
        small
        @click="deleteItem(item)"
      >
        mdi-delete
      </v-icon>
    </template>
    <template v-slot:item.methods="{ item }">
        <v-chip
        v-for="(id) in item.methods"
        :key="id.id"
        class="ma-2"
        small
        >
        {{ id.methods.name.replaceAll('_', ' ')}}
        </v-chip>
    </template>
    <template v-slot:item.name="{ item }">
        {{item.name.toUpperCase()}}
    </template>

    <template v-slot:no-data>
      <v-btn
        color="primary"
        @click="initialize"
      >
        Reset
      </v-btn>
    </template>
  </v-data-table>
</template>

<script>
  export default {
    data: () => ({
      currencys:[],
      selected:[],
      ids:[],
      itemsData:[],
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: 'Countrys',
          align: 'start',
          sortable: true,
          value: 'currency.entity',
        },
        {
          text: 'Currencies',
          align: 'start',
          sortable: true,
          value: 'currency.currency',
        },
        {
          text: 'Codes',
          sortable: true,
          value: 'currency.alphabetic_code',
        },
        { text: 'Payment Method', value: 'methods' },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      desserts: [],
      editedID:null,
      editedIndex: -1,
      editedItem: {
        name: '',
        ids: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
      defaultItem: {
        name: '',
        ids: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
         this.axios
            .get('paymentmethodsCurrencies')
            .then(response => (this.itemsData = response.data));

         this.axios
            .get('paymentmethods')
            .then(response => (this.ids = response.data));
          this.axios
            .get('getCurrencyArr')
            .then(response => (this.currencys = response.data));
      },

      editItem (item) {
        this.editedID= Object.assign({}, item).id
        this.editedIndex = this.itemsData.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.editedItem.name = Object.assign({}, item).currency_id
        let editedItem = Object.assign({}, item).methods
        let arr = [];
        editedItem.forEach((value, ) => {
            arr.push(value.methods);
        });
        this.editedItem.ids = arr
        this.dialog = true
      },

      deleteItem (item) {
        this.editedID= Object.assign({}, item).id
        this.editedIndex = this.itemsData.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.itemsData.splice(this.editedIndex, 1)
         this.axios
          .get('payment-method-delete/'+this.editedID)
          .then(this.editedID = '');

        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        this.sendData(this.editedItem)
        this.initialize ()
        this.close()
      },


        sendData(data) {
            this.axios
            .post("payment-method-create", { data: { data: data,id: this.editedID} })
            .then(() => {
                this.initialize ()
                this.editedID = ''
                this.allerros = [];
                this.sucess = true;
            })
            .catch(() => {
                this.editedID = ''
                this.success = false;
            });
        },
        openNotification: function (type, m, d) {
            this.$notification.config({
                placement: 'topRight',
                top: 35,
                duration: 8,
            });
            this.$notification[type]({
                message: m,
                description: d,
            });
        },
    },
  }
</script>
